.main-headings {
    font-size: 22px;
    font-weight: 600;
}
.desc-titles {
    font-size: 14px;
    color: #8181A5;
}
.contact-details {
    font-size: 16px;
    color: #38384E;
}
.setting-accordion .accordion-button::after {
    display: none; 
}

.setting-accordion .accordion-button::before {
    content: 'x'; 
    font-size: 24px;
    line-height: 1;
    margin-right: 25px;
    color: #7D63EC;
    transition: transform .2s ease-in-out;
}

.setting-accordion .accordion-button.collapsed::before {
    content: '\002B'; 
    transform: rotate(0);
    color: #7D63EC;
}

.setting-accordion .accordion-button:not(.collapsed) {
    color: #7D63EC; 
}

.setting-accordion .accordion-item {
    border: none; 
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 4px !important;
    padding: 3px 20px;
    margin-bottom: 10px;
}

.setting-accordion .accordion-header {
    margin-left: 10px; 
}
.setting-accordion .accordion-button {
    font-size: 18px;
    font-weight: 600;
    color: #20184E !important;
}
.setting-accordion .accordion-button:not(.collapsed) {
    background-color: #fff !important;
    color: #20184E !important;
    font-weight: 600;
    padding: 10px 0px !important;
}
.setting-accordion .accordion-body {
    padding: 10px 10px !important;
    color: #888888 !important;
    font-size: 14px;
}