.fb-items {
  .delete-col {
    width: 70px;
  }

  height: 100%;
  .nav-tabs.custom-nav-tabs {
    height: 35px;
    border: none !important;
  }

  .custom-tab-content-data {
    height: calc(100% - 35px);

    // food Package css
    .food-item-width {
      width: 200px;
    }
    .food-type-width {
      width: 150px;
    }
    .item-cost {
      width: 130px;
      &.invalid {
        border-color: #eb5e5e;
      }
    }
    // food Package css
    .beverage-liquor-package-item-width {
      width: 200px;
    }

    .item-cost {
      width: 130px;
      &.invalid {
        border-color: #eb5e5e;
      }
    }
  }
}
