.guest-data-container {
  height: calc(100% - 96px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.filter-btn-border {
  border: 1px solid #e4e5eb !important;
}

.guest-detail-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  border-radius: 10px;
}
.top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}
.listing-number {
  color: #7d63ec;
}
.guest-heading {
  color: #5a557a;
  font-size: 22px;
  white-space: nowrap;
  font-weight: 600;
}
.btn-text {
  border: none !important;
  border-color: var(--primary-color) !important;
  color: var(--primary-color) !important;
}
.btn-text:hover {
  border: none !important;
  background-color: #fff !important;
  color: var(--primary-color) !important;
}
.custom-offcanvas-btn-wrapper {
  left: 0px;
  position: absolute;
  padding: 0px 20px;
  bottom: 10px;
  /* right: 0px; */
}
.guest-notification-group {
  /* padding: 10px 20px; */
  height: 100%;
}
.address-text {
  color: #adadc6;
}
.info-headings {
  font-size: 20px;
  color: #525361;
}
.form-select:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: none !important;
}
.btn-text:hover svg path {
  stroke: #ffffff; /* White color */
}
.canvas-text {
  color: #7d63ec;
  text-decoration: none;
  cursor: pointer;
}
.custom-guest-table .e-grid .e-rowcell {
  font-size: 14px !important;
  padding: 14px 20px !important;
}
.custom-guest-table .e-headertext {
  font-size: 14px !important;
  color: #1c1d21 !important;
  font-weight: 600 !important;
}
.custom-guest-table .e-grid {
  border-width: 0px !important;
}
.custom-guest-table .e-grid .e-gridheader {
  border-bottom-color: #e0e0e0;
  border-top: 0px !important;
  border-bottom-width: 2px !important;
}
.header-seperator {
  background-color: #e4e5eb;
  width: 100%;
  height: 1px;
}
.btns-wrapper {
  white-space: nowrap;
}

.offcanvas-body-container {
  position: relative;
  height: 100%;
}

.offcanvas-body-container .offCanvas-body {
  height: calc(100% - 70px);
}

.offcanvas-body-container .e-datepicker {
  text-align: end;
}

.offCanvas-footer {
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0 20px;
  position: absolute;
  background: #f5f5fa;
  height: 70px;
}

.pagination-table {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100% - 60px);
}

.custom-pagination {
  font-family: "Lato";
  display: flex;
  gap: 6px;
  font-size: 14px;
}

.custom-pagination .page-btn {
  background: #fbfafd;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 2;
  transition: 0.2s all;
  cursor: pointer;
}

.custom-pagination .page-btn:hover {
  background: rgba(94, 129, 244, 0.1);
  color: #7d63ec;
}

.custom-pagination .page-btn.active {
  background: rgba(94, 129, 244, 0.1);
  color: #7d63ec;
}

.custom-pagination .page-btn-number {
  padding: 10px;
  height: 38px;
  width: 38px;
}

.custom-pagination .page-prev-next {
  padding: 10px 19px;
  height: 38px;
  color: #8181a5;
}

.guest-details {
  height: calc(100% - 156px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.enquiry-btn {
  width: 164px;
  height: 40px;
  background: #7d63ec !important;
  padding: 10px, 20px, 10px, 20px;
  border: none;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.enquiry-btn-text {
  color: #ffffff;
  font-size: 15px;
  font-weight: 700;
  line-height: 20px;
  margin-left: 5px;
}

/* Guest notification */

.guest-notification-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: calc(100% - 24px);
  width: 100%;
  border-radius: 10px;
}

.guest-notification-heading {
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: left;
}
.guest-notification-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;
}
.guest-notification {
  padding: 10px 10px 12px 10px;
  border-radius: 8px;
  border: 0.5px solid #b3b5e1;
  width: 100%;
  background: #f4f4fa80;
}

.guest-notification .guest-notification-heading {
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: 0px;
  text-align: left;
  color: var(--primary-color);
  display: flex;
  flex-direction: row;
  row-gap: 10px;
}

.guest-notification .notification-text {
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 0px;
  text-align: left;
  margin-top: 10px;
  color: #1c1d21;
}

.guest-empty-notification {
  font-weight: bold;
  font-size: 16px;
  color: #323232;
}

.guest-mgmt-date .e-date-wrapper {
  display: flex !important;
  flex-direction: row-reverse !important;
}

.guest-mgmt-date .e-input-group input.e-input,
.e-input-group.e-control-wrapper input.e-input {
  text-align: left !important;
}

.guest-mgmt-date .e-input-group .e-clear-icon,
.e-input-group.e-control-wrapper .e-clear-icon {
  position: absolute;
  top: 5px;
}

.e-sortnumber{
  display: none !important;
}
