.calender {
  height: calc(100% - 96px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.month-heading {
  font-size: 16px;
  color: #8181a5;
}

.desc-text {
  font-size: 14px;
}

.summary-detail-groups {
  height: 100%;
  width: 100%;
  padding: 0 20px;
  border-left: 1px solid #c2c8d6;
}

.summary-detail-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.seperators-bord {
  margin-left: -20px;
}

.text-red {
  color: #ff8686;
}

.text-green {
  color: #1cb161;
}

.heading-date {
  color: #7d63ec;
}

.summary-heading-wrapper {
  display: flex;
  justify-content: space-between;
}

.e-toolbar-right {
  display: none !important;
}

.calendor-event-wrapper {
  display: flex;
  justify-content: end;
  gap: 20px;
  position: relative;
  top: 30px;
  right: 10px;
  z-index: 1;
  margin-left: 220px;
}

.indicate-box1 {
  background-color: #fff;
  width: 16px;
  height: 16px;
  border: 1px solid #d7dedf;
  border-radius: 4px;
}

.indicate-box2 {
  background-color: #7d63ec;
  width: 16px;
  height: 16px;
  border: 1px solid #7d63ec;
  border-radius: 4px;
}

.indicate-box3 {
  background-color: #ff8686;
  width: 16px;
  height: 16px;
  border: 1px solid #ff8686;
  border-radius: 4px;
}

.indicate-box4 {
  background-color: #f1f1f8;
  width: 16px;
  height: 16px;
  border: 1px solid #d7dedf;
  border-radius: 4px;
}

.calendor-event {
  display: flex;
  align-items: center;
  gap: 10px;
}

.calendor-event h6 {
  font-size: 13px;
}

.operational-desc {
  color: #888888;
  font-size: 14px;
}

.operational-heading {
  color: #433f5c;
  font-weight: 600;
  font-size: 15px;
}

.reminder-heading {
  color: #433f5c;
  font-weight: 600;
  font-size: 15px;
}

.reminder-badge {
  background-color: #f5f4ff;
  padding: 8px 15px;
  border-radius: 25px;
  font-size: 13px;
  margin-left: 10px;
}

.reminder-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.reminder-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.upcoming-badge {
  background-color: #f19a031a;
  color: #f19b03;
  padding: 8px 15px;
  border-radius: 25px;
  font-size: 13px;
}

.user-desc {
  color: #58596b;
  font-size: 14px;
}

.e-schedule .e-month-view .e-date-header-wrap table td,
.e-schedule .e-month-agenda-view .e-date-header-wrap table td {
  background: #f7f5fd !important;
  border-color: rgba(0, 0, 0, 0.12);
  border-style: solid;
  border-width: 0 0 1px 1px;
  color: rgba(0, 0, 0, 0.87);
  font-size: none;
  text-align: center !important;
  text-transform: none;
  text-align: center;
  border-top: none !important;
  padding: 10px;
}

.e-schedule
  .e-schedule-toolbar
  .e-toolbar-items
  .e-toolbar-item.e-date-range
  .e-tbar-btn
  .e-tbar-btn-text {
  font-size: 18px !important;
  font-weight: 600 !important;
}
.e-schedule .e-schedule-toolbar {
  box-shadow: none !important;
  margin-bottom: 10px !important;
  min-height: 45px !important;
}

.e-schedule {
  border: 0px !important;
}

.e-schedule .e-month-view .e-work-cells {
  height: 120px !important;
}

.e-schedule .e-month-view .e-appointment {
  background: #fff !important;
  border-top: 2px solid #a384ff !important;
  border-left: 0 !important;
  border-right: 0 !important;
  border-bottom: 0 !important;
  border-radius: 0px !important;
  color: #7d63ec !important;
  height: 25px !important;
}

.e-schedule .e-month-view .e-selected-cell,
.e-schedule .e-month-agenda-view .e-selected-cell {
  background: #fff !important;
}

.e-schedule .e-month-view .e-current-date .e-date-header,
.e-schedule .e-month-agenda-view .e-current-date .e-date-header {
  background: #009896 !important;
  border-radius: 4px !important;
  width: fit-content !important;
}

.e-schedule .e-month-view .e-date-header {
  height: 24px !important;
  margin: 10px 0px 0px !important;
  text-align: center;
  width: 100% !important;
}

.e-schedule .e-text-center {
  text-align: left !important;
}

.e-schedule .e-month-view .e-appointment .e-appointment-details .e-subject {
  padding: 1px 8px !important;
}

.num-digit {
  color: #8181a5;
  margin-left: 10px;
}

.booking-heading p {
  font-size: 15px;
  font-weight: 600;
}

.setting-sec {
  display: flex;
  justify-content: end;
  align-items: start;
}

.date-head {
  font-size: 14px;
  color: #888888;
}

.user-head {
  font-size: 15px;
} 

.e-calendar .e-content td.e-selected span.e-day,
.e-bigger.e-small .e-calendar .e-content td.e-selected span.e-day {
  background-color: #fff !important;
  color: #7d63ec !important;
}
.e-btn.e-flat.e-primary,
.e-css.e-btn.e-flat.e-primary {
  color: #7d63ec !important;
}

.e-btn.e-flat.e-primary:hover,
.e-css.e-btn.e-flat.e-primary:hover {
  color: #7d63ec !important;
}
.e-schedule .e-month-view .e-date-header-wrap table td.e-current-day,
.e-schedule .e-month-agenda-view .e-date-header-wrap table td.e-current-day {
  color: #7d63ec !important;
}
.e-work-cells.e-work-days.e-current-date {
  display: flex;
  justify-content: center;
  width: 100% !important;
}
.e-schedule .e-month-view .e-work-cells:hover,
.e-schedule .e-month-agenda-view .e-work-cells:hover,
.e-schedule .e-month-view .e-work-cells:active,
.e-schedule .e-month-agenda-view .e-work-cells:active {
  border: 1px solid #7d63ec !important;
}

.schedule-cell-customization.e-schedule
  .e-month-view
  .e-work-cells:not(.e-work-days) {
  background-color: #f8f8fb;
}
.venue-headings {
  font-size: 16px;
  font-weight: 500;
}
.e-schedule .e-timeline-month-view .e-resource-left-td {
  width: 150px !important;
}

.e-schedule .e-timeline-view .e-appointment .e-subject,
.e-schedule .e-timeline-month-view .e-appointment .e-subject {
  display: none !important;
}
.e-schedule .e-timeline-view .e-appointment .e-time,
.e-schedule .e-timeline-month-view .e-appointment .e-time {
  display: none !important;
  line-height: 1.4;
}
.e-schedule .e-timeline-month-view .e-appointment {
  border-radius: 4px !important;
  height: 30px !important;
}

@media screen and (max-width: 575px) {
  .calendor-event-wrapper {
    display: block;
    margin-left: 0px;
    margin-bottom: 40px;
  }

  .calendor-event {
    margin-bottom: 15px;
  }
}
