.package-details {
  background: white;
  border-radius: 7px;
  padding: 16px 24px 32px;
  .select-packages-details {
    display: flex;
    justify-content: center;

    .content {
      margin: 128px 0px;
      .title {
        font-size: 24px;
        color: #323232;
        font-weight: 700;
      }
      .options-container {
        margin-top: 72px;
        display: flex;
        gap: 80px;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        .options-label {
          color: #5a557a;
          font-size: 18px;
          font-weight: 500;
        }
      }
      .option {
        height: 180px;
        width: 190px;
        transition: box-shadow 0.2s, opacity 0.2s;
        border-radius: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0.5;
      }
      .option:hover {
        width: 190px;
        box-shadow: rgba(125, 99, 236, 0.2) 0px 4px 16px 0px;
        font-weight: 900;
        color: #7d63ec;
        opacity: 1;
      }
    }
  }

  .package-details-header-container {
    margin-bottom: 22px;
    .title-header {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 8px;
      padding: 8px 0px;
      .title {
        color: #5a557a;
        font-weight: 700;
        font-size: 16px;
      }
      .line-diff {
        border-bottom: 0.5px solid #e7e4ff;
        flex-grow: 1;
      }
    }
  }
  .package-details-main-container {
    margin-bottom: 22px;
    label {
      font-size: 13px !important;
      font-weight: 400;
    }
    .dash-line-diff {
      border-bottom: 1px dashed #e4e5eb;
      width: 250px;
    }

    // table style
    .simple-table {
      .border-right-2px {
        border-right: 2px solid #e4e5eb;
      }
      thead.bold-header tr th {
        font-weight: bold;
        text-transform: uppercase;
      }

      thead tr {
        border-radius: 4px;
        height: 50px;
        th {
          text-transform: uppercase;
          vertical-align: middle;
          white-space: nowrap;
          border-bottom: 1px solid #d4cfff;
          background: #f9f9ff;
          color: #1c1d21;
          font-size: 14px;
        }
      }
      tbody tr td {
        color: #323232;
        font-weight: 500;
        font-size: 15px;
        border-bottom: 1px solid #e4e5eb;
      }
      tbody tr.primary-background td {
        background: #8b6ffa;
        color: white;
        padding: 10px 20px;
        font-weight: bold;
        font-size: 16px;
      }
    }

    .bottom-total {
      border-bottom: 1px solid #8b6ffa;
      display: flex;
      justify-content: space-between;
      background: #ede7ff;
      padding: 10px 20px;
      border-radius: 4px;
      color: #323232;
      font-weight: bold;
      font-size: 16px;
    }

    .bottom-total-primary {
      background: #8b6ffa;
      color: white;
      display: flex;
      padding: 10px 20px;
      justify-content: space-between;
      border-radius: 4px;
      font-weight: bold;
      font-size: 16px;
    }

    .title-header-container {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 8px 0px;

      .title {
        color: #5a557a;
        font-weight: 700;
        font-size: 16px;
      }
      .line {
        border-bottom: 1px solid #e7e4ff;
        flex-grow: 1;
      }
    }

    .add-on-count {
      color: #58596b;
      font-weight: 700;
      font-size: 15px !important;
      display: flex;
      justify-content: center;
      .inc-dec-container {
        width: 80px;
        height: 36px;
        padding: 8px;
        border-radius: 4px;
        border: 1px solid #e4e5eb;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 6px;
      }
    }
  }
}

.status.active-inquiry {
  background-color: #1cb161;
  border: none;
  color: white;
  border-radius: 24px;
  font-size: 12px;
  font-weight: 500;
  padding: 2px 12px;
}

.header-display-name {
  font-size: 20px;
}

.package-details-back-arrow {
  display: flex;
  align-items: center;
  margin-top: 12px;
  font-weight: 700;
  color: #433f5c;
  font-size: 18px;
}
