.reminders {
  height: calc(100% - 96px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.reminder-group {
  padding: 0px 10px;
}

.today-heading {
  font-size: 18px;
  font-weight: 600;
}
.main-headings {
  font-size: 22px;
  font-weight: 600;
}

.reminder-badges {
  background-color: #5e81f41c;
  padding: 8px 8px;
  border-radius: 25px;
  font-size: 9px;
  color: #5e81f4;
  margin-left: 6px;
}

.reminder-heading {
  color: #433f5c;
  font-weight: 600;
  font-size: 15px;
}
.modal-heading {
  color: #433f5c;
  font-weight: 600;
  font-size: 16px;
}

.date-head {
  color: #666666;
}

.summary-title {
  font-size: 15px;
  color: #323232;
  font-weight: 600;
}

.border-end {
  border-right: 1px dashed #dee2e6 !important;
}

.btn-check:checked + .btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check) + .btn:active {
  border-color: transparent !important;
}

.reminder-card {
  padding: 10px 15px;
  border: 1px solid #e4e5eb;
  border-radius: 8px;
  margin-bottom: 20px;
}

.heading-border {
  border-top: 1px solid #e4e5eb;
}
.form-select:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: none !important;
}
.custom-form-select {
  border: none !important;
  border-bottom: 1px solid #dee2e6 !important;
  border-radius: 0 !important;
  padding-left: 0 !important;
}
