.dropdown-wrapper .e-input-group {
  border-bottom: 1px solid #dee2e6 !important;
}
.table-dropdown-wrapper .e-input-group {
  width: 100%;
  border: 1px solid #e5e5e5 !important;
  padding: 1px 10px;
  border-radius: 6px !important;
}
.table-dropdown-wrapper .e-input-group-icon {
  background-size: contain;
}
.table-dropdown-wrapper .e-input-group-icon::after,
.table-dropdown-wrapper .e-input-group-icon::before {
  content: "";
}

