.canvas-heading {
  color: #5a557a;
}
.custom-form-label {
  color: #888888;
  font-size: 14px;
}
.custom-form-select {
  border: none !important;
  border-bottom: 1px solid #dee2e6 !important;
  border-radius: 0 !important;
  padding-left: 0 !important;
}
.canvas-bottom {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 15px;
}
.canvas-bottom-wrapper {
  position: absolute;
  bottom: 20px;
}
.canvas-btn {
  background-color: #a2a2b1 !important;
  border-color: #a2a2b1 !important;
  color: #fff !important;
  padding: 5px 20px !important;
}

/* Inquiry Action button */
.inquiry-action-split-button .e-control {
  padding: 7px;
  background-color: #7d63ec;
  border-radius: 8px;
  display: flex;
  align-items: center;
}
.inquiry-action-split-button .e-control:hover {
  background-color: #7d63ec;
  color: #ffffff;
}
.inquiry-action-split-button .e-control {
  color: #ffffff;
  display: flex;
  align-items: center;
  gap: 5px;
}
.inquiry-action-split-button .e-icon-btn {
  border-left: 1px solid white;
  padding: 12px 12px 12px 12px !important;
}
.inquiry-action-split-button .e-icon-btn:hover {
  border-left: 1px solid white !important;
}
.inquiry-action-split-button .e-btn.e-active {
  background-color: #7d63ec !important;
}
