.inquiry {
  height: calc(100% - 154px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.inquiry-booking {
  height: calc(100% - 96px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.offcanvas-btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.inquiry-top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.inquiry-btns-wrapper {
  display: flex;
  align-items: center;
}

.read-only {
  opacity: 0.4;
}

.overflow-ellipse {
  text-overflow: ellipsis;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
}

.form-title-wrapper {
  display: flex;
  align-items: center;
  .title {
    margin: 0 10px 0 0;
    width: fit-content;
    white-space: nowrap;
  }
  .line {
    height: 1px;
    width: 100%;
    background-color: #bcb5f3;
  }
}

.add-new-wrapper-primary {
  display: flex;
  align-items: center;
  background: #f5f4ff;
  border-radius: 8px;
  height: 34px;
  font-size: 13px;
}
.slot-select-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.slot-detail-wrapper {
  background-color: #f5f4ff;
  display: flex;
  align-items: center;
  font-size: 14px;
  padding: 10px;
  border-radius: 7px;
  width: fit-content;
}
.slot-select-wrapper .form-select {
  background-color: #f5f4ff !important;
  width: fit-content !important;
  border: none;
}
.details-tab .nav-tabs {
  border: none;
}
.details-tab .nav-link {
  padding-bottom: 5px;
  font-size: 15px;
  border-bottom: 2px solid #ffffff !important;
  transition: 0.1s;
}
.details-tab .nav-item {
  display: flex;
  align-items: center;
  gap: 5px;
}
.notes-wrapper {
  position: relative;
}
.notes-container {
  overflow-y: auto;
  max-height: 500px;
}
.add-note-form-wrapper {
  height: 500px;
}
.notes-container::-webkit-scrollbar {
  display: none;
}
.note-wrapper {
  cursor: pointer;
  padding: 15px;
  border: 1px solid #e4e5eb;
  border-radius: 10px;
  margin-bottom: 15px;
}
.note-wrapper p {
  font-size: 14px;
  color: #888888;
}
.note-wrapper .edit {
  font-size: 13px;
  color: #888888;
}
.note-wrapper .title {
  font-size: 15px;
  color: #58596b;
}
.note-wrapper hr {
  background-color: #e4e5eb;
}
.note-wrapper .calender {
  font-size: 12px;
  color: #666666;
}
.note-badge {
  color: #ffffff;
  font-size: 11px;
  padding: 1px 11px;
  border-radius: 50px;
  background-color: #e5960a;
}
.venue-details-badge {
  color: #666666;
  font-size: 11px;
  padding: 1px 11px;
  border-radius: 50px;
  border: 1px solid #666666;
}
.notes-action-btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* position: absolute;
  bottom: 0; */
  width: 100%;
}
.notes-action-btns .txt {
  font-size: 14px;
}
.grand-banquet-wrapper .accordion-button {
  display: flex;
  align-items: center;
  padding: 0 !important;
  padding-bottom: 15px !important;
}
.grand-banquet-wrapper .accordion-body {
  padding-top: 0 !important;
}
.grand-banquet-wrapper .accordion-button .title {
  color: #5a557a;
  font-weight: 600;
  white-space: nowrap;
  margin-right: 10px;
}
.grand-banquet-wrapper .accordion-button .line {
  width: 100%;
  height: 1px;
  background-color: #e7e4ff;
  margin-right: 10px;
}
.accordion-button::after {
  background-image: url(../../../../assets/img/down-arrow.png) !important;
  width: 10px !important;
  background-position: center;
  background-size: contain !important;
  margin-left: 10px !important;
}
.grand-banquet-wrapper .accordion-button:not(.collapsed)::after {
  background-image: url(../../../../assets/img/down-arrow.png) !important;
  width: 10px;
  background-position: center;
  background-size: contain;
  margin-left: 10px;
}
.design-wrapper img {
  width: 100px;
}
.inquiry-cancelled-error {
  padding: 15px 20px 2px;
  border-radius: 10px;
  border: 1px solid #eb5e5e;
  background-color: #fff2f2;
}
.waitlist-alert {
  font-size: 15px;
  display: flex;
  align-items: center;
  padding: 10px 20px;
  border-radius: 10px;
  border: 1px solid #f2ae34;
  background-color: #fff7e8;
}
.inquiry-disposed {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 50px 0;
}
.inquiry-disposed img {
  width: 180px;
}
.bind-note-desc {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
}
.venue-slot-wrapper {
  display: flex;
  align-items: center;
  font-size: 14px;
}
.venue-slot-wrapper h6 {
  font-size: 14px;
}
.venue-banquet-wrapper {
  padding: 14px 16px;
  border: 1px solid #e4e5eb;
  border-radius: 7px;
}
.no-venue-added-error {
  display: flex;
  align-items: center;
  border: 1px solid #eb5e5e;
  border-radius: 7px;
  padding: 5px 10px;
  font-size: 13px;
}
.availability-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #f5f4ff;
  border-radius: 10px;
}
.not-available-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #f5f4ff;
  border-radius: 10px;
}
.available-check:checked {
  background-image: url(../../../../assets/img/tick-white.png);
  background-size: auto;
  background-position: center;
}
.date-time-text {
  font-size: 15px;
}
.custom-hr {
  color: #e4e5eb;
}
.custom-filter-accordion .accordion-button {
  padding: 13px 0 !important;
  color: #888888 !important;
  border-bottom: 1px solid #e4e5eb;
}
.dropdown-title {
  font-size: 15px;
  margin-left: 9px;
}
.reminder-form-wrapper {
  overflow-x: hidden;
}
.bind-reminder-desc {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.details-cards-wrapper {
  overflow-y: auto;
  max-height: 500px;
}
.details-cards-wrapper::-webkit-scrollbar {
  display: none;
}

.guest-autocomplete-container {
  .e-ddl.e-input-group.e-control-wrapper .e-input {
    min-height: 0 !important;
    padding: 0px 0 4px !important;
    font-size: 1rem;
    font-family: "Lato", sans-serif;
  }

  .e-ddl.e-input-group input.e-input::selection {
    color: #fff !important;
    background: #745be3 !important;
  }

  .e-control-wrapper {
    border-color: #dee2e6 !important;
  }
}
.autocomplete-list-item {
  padding: 10px;
  .user-img {
    height: 36px;
    width: 36px;
    border-radius: 8px;
    background: rgb(245, 245, 250);
  }
  .user-data {
    height: 36px;
    .user-name {
      font-size: 12px;
      font-weight: 400;
      line-height: 14px;
      color: #888888;
      margin-top: 2px;
    }
    .user-phone {
      font-size: 15px;
      font-weight: 400;
      line-height: 18px;
      color: #745be3;
    }
  }
}

.selectedGuestSpinner {
  z-index: 500;
  position: relative;
}
