.time-details {
  .day-column-color {
    color: #525361;
    font-size: 15px;
    font-weight: 600;
  }
  .day-width-col {
    min-width: 150px;
  }
  .time-picker-container {
    min-width: 200px;
    max-width: 300px;
    gap: 8px;
    display: flex;
    align-items: center;

    span {
      width: 120px;
      flex-grow: 1;
    }
  }
}
