.header {
  min-height: 72px;
  display: flex;
  align-items: center;
  transition: 0.3s;
  font-size: 16px;
  justify-content: space-between;
}
.heading-title {
  font-size: 20px;
  color: #433f5c;
  font-weight: 600;
}
.users-profile {
  color: #7d63ec;
  font-size: 18px;
  font-weight: 600;
}

/* Inquiry and Bookings */
.name-wrapper {
  font-size: 17px;
}
.inquiry-status-badge {
  width: fit-content;
  color: white;
  font-size: 12px;
  padding: 3px 13px;
  border-radius: 50px;
}
.active-inquiry {
  background-color: #1cb161;
}
.duplicate-active-inquiry {
  background-color: #32cb66;
}
.cancelled-inquiry {
  background-color: #eb5e5e;
}
.waitlist {
  background-color: #f2ae34;
}
.booking-confirmed {
  background-color: #5e81f4;
}
.booking-completed {
  background-color: #7d63ec;
}
.canvasbtn {
  background-color: #7d63ec !important;
  border-color: #7d63ec !important;
  padding: 8px 20px !important;
}
.canvasbtn-toggle {
  background-color: #7d63ec !important;
  border-color: #7d63ec !important;
  padding: 8px 14px !important;
}
.btn-group > .btn-group:not(:first-child) > .btn,
.btn-group > .btn:nth-child(n + 3),
.btn-group > :not(.btn-check) + .btn {
  border-left: 1px solid #fff !important;
}
.btn-group-vertical > .btn-check:checked + .btn,
.btn-group-vertical > .btn-check:focus + .btn,
.btn-group-vertical > .btn.active,
.btn-group-vertical > .btn:active,
.btn-group-vertical > .btn:focus,
.btn-group-vertical > .btn:hover,
.btn-group > .btn-check:checked + .btn,
.btn-group > .btn-check:focus + .btn,
.btn-group > .btn.active,
.btn-group > .btn:active,
.btn-group > .btn:focus,
.btn-group > .btn:hover {
  border-right: 1px solid #fff !important;
}
.dropdown-item.active,
.dropdown-item:active {
  color: #212529 !important;
  text-decoration: none;
  background-color: #f1f6f9 !important;
}

.calendar-action
  .calendar-select
  .e-input-group.e-control-wrapper:not(.e-success):not(.e-warning):not(
    .e-error
  ):not(.e-float-icon-left) {
  border-color: rgba(0, 0, 0, 0.42);
  border: 1px solid #7d63ec !important;
  padding: 5px 10px !important;
  border-radius: 6px !important;
}
.calendar-select .e-input-group,
.e-input-group.e-control-wrapper,
.e-float-input,
.e-float-input.e-input-group,
.e-float-input.e-control-wrapper,
.e-float-input.e-input-group.e-control-wrapper {
  background: transparent;
  /* color: #7d63ec !important; */
}
.calendar-action .calendar-select .e-input-group .e-input-group-icon {
  color: #7d63ec !important;
}
.calendar-action .calendar-select ::placeholder {
  font-size: 14px !important;
  color: #7d63ec !important;
}
.calendar-select .dropdown-wrapper {
  bottom: -3px !important;
}
.e-input-group.e-control-wrapper:not(.e-float-icon-left):not(
    .e-float-input
  )::after {
  height: 0px !important;
}
.e-input-group:not(.e-float-icon-left):not(.e-float-input)::before {
  height: 0px !important;
}
.calendar-select .e-control {
  color: #7d63ec !important;
}

.header-container {
  padding: 16px 0px;
}
