.venue-tabs .nav-link {
  padding-bottom: 2px;
  font-size: 14px;
}

.venue-tabs .nav-tabs {
  border: none;
}

.venue-tabs .nav-link.active {
  font-weight: 700;
  font-size: 14px;
  color: #7d63ec !important;
  border-bottom: 3px solid #7d63ec !important;
}

.venue-tabs .nav-link:hover {
  color: #7d63ec !important;
}

.venue-tabs .nav-link.active:focus,
.venue-tabs .nav-link.active:focus-visible {
  color: #7d63ec !important;
}

/* venue details accordian */
.venue-details .title {
  color: #5a557a;
  font-weight: 600;
  margin-right: 10px;
  white-space: nowrap;
}

.venue-details .line {
  width: 100%;
  height: 1px;
  background-color: #e7e4ff;
  margin-right: 10px;
}

/* Venue Info Accordian */
.venue-info-offcanvas {
  width: 500px !important;
}

.border-bottom-venue-info {
  border-bottom: 1px solid #fbfafd;
}

.color-009896 {
  color: #009896;
  font-weight: 500;
  font-size: 13px;
}

.display-label-data {
  font-weight: 500;
  font-size: 15px;
}

/* Amenities Accordian  */
.display-amenities-group {
  display: flex;
  column-gap: 32px;
  flex-wrap: wrap;
}
